.sep,
.sep--white {
    height: 1px;
    width: 100%;
    margin-top: 0;
    margin-bottom: 0;
    border: 0;
    background-color: rgba(0, 0, 0, 0.1);
}
.sep--white {
    background-color: rgba(255, 255, 255, 0.6);
}
.hidden {
    display: none;
}
section {
    position: relative;
}
.section-padding {
    padding: 2em 1em;
}
@media (min-width: 32em) {
    .section-padding {
        padding: 3em 2em;
    }
}
@media (min-width: 54em) {
    .section-padding {
        padding-right: 1em;
        padding-left: 1em;
    }
}
@media (min-width: 91em) {
    .section-padding {
        padding-top: 4em;
        padding-bottom: 4em;
    }
}
.section-padding--double {
    padding: 4em 1em;
}
@media (min-width: 32em) {
    .section-padding--double {
        padding: 6em 2em;
    }
}
@media (min-width: 54em) {
    .section-padding--double {
        padding-right: 1em;
        padding-left: 1em;
    }
}
@media (min-width: 91em) {
    .section-padding--double {
        padding-top: 8em;
        padding-bottom: 8em;
    }
}
.section-padding--half {
    padding: 1em;
}
